import { Link } from "react-scroll";
import { Navbar, Nav, NavItem } from 'react-bootstrap';
import resume from "../img/AnkitKumar.pdf";

function NavBar(props) {

    var divStyle = {
        padding: "20px",
        backgroundColor: "black",
        textAlign: "right",
        position: "sticky",
        fixed:"top",
      };
      var aStyle = {
        color: "white",
        marginRight: "50px",
        textDecoration: "none",
      };
      
    return (
      <Navbar  sticky="top"> 
    <div id="nav-menu" style={divStyle}>
      
       <Link activeClass="active" smooth spy to="home">  
      <a style={aStyle}  class="nav-link home">
        Home
      </a>
      </Link>
      <Link activeClass="active" smooth spy to="about"> 
      <a style={aStyle}  class="nav-link about">
      About
      </a>
      </Link>
      <Link activeClass="active" smooth spy to="skills"> 
      <a style={aStyle}   class="nav-link skills">
      Skills
      </a>
      </Link>
      <Link activeClass="active" smooth spy to="projects"> 
      <a style={aStyle}  class="nav-link projects">
      Projects
      </a>
      </Link>
      <Link activeClass="active" smooth spy to="contact"> 
      <a style={aStyle}   class="nav-link contact">
      Contact
      </a>
      </Link>
      

      <a style={aStyle}  class="nav-link resume" href={resume}  target='_blank' id="resume-button-1">
      Resume 
      </a>
      
      
      </div>
      </Navbar>
      
    );
  }
  
  export default NavBar;